import { httpGet, httpPost, getUrl } from './http.js';
history.pushState(null, null, null);
window.onpopstate = function () {
	history.pushState(null, null, null);
};
var interval;
function oauth() {
	var auth = sessionStorage.Authorization;
	if (!auth) {
		var url = getUrl("/code.html#" + location.pathname + location.search);
		httpGet('/wechat/portal/oauth/url',
			{ url: url },
			function (response) {
				window.location.replace(response);
			},
			function (xhr, type) {
				var getUrlNumber = sessionStorage.GetOauthUrlNumber;
				if (getUrlNumber) {
					getUrlNumber = parseInt(getUrlNumber) + 1;
				}
				else {
					getUrlNumber = 1;
				}

				if (getUrlNumber > 3) {
					window.stop ? window.stop() : document.execCommand("Stop");
				}
				else {
					sessionStorage.GetOauthUrlNumber = getUrlNumber;
					window.location.reload();
				}
			});
	}
	else {
		var url = location.pathname;
		alert("oauth currentUrl:"+url);
		if (url.search(/fillout1.html/i) > -1) {
			var fillCount = sessionStorage.FillCount;
			//无可用填报次数
			if (fillCount && fillCount < 1) {
				url = url.replace(new RegExp('fillout1.html', "igm"), 'pay.html');
				alert("fille url:" + url);
				window.location.replace(url);
				return;
			}

		}
		// normal
		$('body').removeAttr('style');
		interval = self.setInterval(function () {
			httpGet('/refresh/jwt',
				null,
				function (response) {
				},
				function (xhr, type) {
				});
		}, 1000 * 60 * 15);
		sessionStorage.LocationHref = location.href


	}
	//}
}

var ua = window.navigator.userAgent.toLowerCase();
if (ua.match(/MicroMessenger/i) == 'micromessenger') {
	$('body').attr('style', 'display: none');
	oauth();
} else {
	console.log('Not Weixin!');
	window.stop ? window.stop() : document.execCommand("Stop");
}


